    bottom: 45px;
  }

  .hero-image-text-container {
    float: left;
  }

  .hero-info-container {
    display: block;
    background-color: white;
    box-shadow: 2px 4px 5px grey;
    padding: 25px;
  }

  .hero-about-me {
    font-size: clamp(2rem, 5vw + 5px, 6rem);
    text-align: center;
  }

  .hero-paragraph {
    font-size: 1.7rem;
  }

  .drake-header-text {
    position: absolute;
    top: -60px;
    bottom: 0px;
    left: 50px;
    font-size: 3rem;
    text-wrap: nowrap;
    z-index: 1;
  }

  .hero-icons {
    display: none;
  }

  main {
    margin-top: 0rem;
    padding: 20px;
  }

  .main-heading-text {
    font-size: 2.5rem;
  }

  .projects-container {
    grid-template-columns: 1fr 1fr;
  }

  .screenshot {
    height: 220px;
  }

  .proj-info {
    height: 150px;
  }

  .proj-description {
    font-size: 1rem;
    line-height: normal;
  }

  footer {
    margin-top: 11rem;
    padding: 2.5rem;
    flex-direction: row;
    justify-content: space-evenly;
  }

  .footer-image {
    height: 300px;
    width: 500px;
  }

  .footer-main-text {
    font-size: 2rem;
  }

  .footer-secondary-text {
    font-size: 1.7rem;
    margin-top: 2rem;
  }

  .footer-links {
    margin-top: 2rem;
    gap: 1.5rem;
  }
}

/* Desktop Query */

@media screen and (min-width: 1000px) {
  .hero-background-decoration {
    transform: skew(0deg, -10deg);
  }

  .hero-container {
    display: flex;
    flex-direction: row;
    margin-top: 0;
    padding: 3rem;
  }

  .hero-info-container {
    display: flex;
    flex-direction: column;
    width: clamp(30vw, 50vw, 60vw);
  }

  .hero-icons {
    display: flex;
    align-self: flex-end;
  }

  .hero-about-me {
    font-size: clamp(2rem, 2vw + 15px, 6rem);
    text-align: start;
    margin-top: 0;
  }

  .hero-paragraph {
    font-size: clamp(1rem, 1vw + 7px, 6rem);
  }

  .hero-image {
    height: clamp(25vw, 35vw, 45vw);
    width: auto;
    position: static;
  }

  .drake-header-text {
    position: absolute;
    top: auto;
    bottom: 10px;
    left: 10px;
    font-size: clamp(1rem, 4vw, 5rem);
    text-wrap: wrap;
  }

  .projects-container {
    grid-template-columns: 1fr 1fr 1fr;
  }

  footer {
    align-items: center;
  }

  .footer-secondary-text {
    margin-top: 2rem;
  }

  .footer-links {
    margin-top: 2rem;
  }

  .footer-image {
    height: 350px;
    width: 350px;
  }
}
