-template-columns: 100%;\n  gap: 4rem;\n}\n\n.proj-info {\n  box-shadow: 2px 4px 5px grey;\n}\n\n.screenshot {\n  height: 250px;\n}\n\n.proj-links {\n  display: flex;\n  gap: 1rem;\n}\n\n.proj-links img {\n  width: 30px;\n}\n\n.proj-header-info {\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n  padding-top: 1rem;\n}\n\n.proj-header-info > h4 {\n  font-size: 1.4rem;\n  font-family: poppins, Arial, Helvetica, sans-serif;\n}\n\n.proj-description {\n  padding: 1rem;\n  font-size: 1.1rem;\n  font-family: teacher, Georgia, \"Times New Roman\", Times, serif;\n  line-height: 1.5rem;\n}\n\n.proj-1,\n.proj-2,\n.proj-3,\n.proj-4,\n.proj-5,\n.proj-6,\n.footer-image {\n  background-size: cover;\n  background-position: center;\n  background-repeat: no-repeat;\n}\n\n.proj-1 {\n  background-image: url(./assets/battleship-screenshot.png);\n}\n\n.proj-2 {\n  background-image: url(./assets/weather-app-screenshot.png);\n}\n\n.proj-3 {\n  background-image: url(./assets/todo-list-screenshot.png);\n}\n\n.proj-4 {\n  background-image: url(./assets/tic-tac-toe-screenshot.png);\n}\n\n.proj-5 {\n  background-image: url(./assets/restaurant-page-screenshot.png);\n}\n\n.proj-6 {\n  background-image: url(./assets/library-screenshot.png);\n}\n\n/* Footer Styles */\n\nfooter {\n  margin-top: 6rem;\n  background-color: blue;\n  display: flex;\n  flex-direction: column;\n}\n\n.footer-info {\n  padding: 30px;\n}\n\n.footer-main-text {\n  font-size: clamp(2rem, 5vw + 5px, 6rem);\n  font-family: poppins, Arial, Helvetica, sans-serif;\n  color: white;\n}\n\n.footer-secondary-text {\n  margin-top: 1rem;\n  font-size: clamp(1rem, 5vw, 4rem);\n  line-height: 1.5em;\n  font-family: teacher, Georgia, \"Times New Roman\", Times, serif;\n  color: white;\n}\n\n.footer-links {\n  margin-top: 1rem;\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n}\n\n.footer-links img {\n  height: 50px;\n}\n\n.footer-image {\n  background-image: url(./assets/jester.png);\n  height: 250px;\n  width: 100vw;\n}\n\n/* Tablet Query */\n\n@media screen and (min-width: 700px) {\n  .hero-container {\n    display: block;\n    padding: 4rem;\n    margin-top: 80px;\n    position: relative;\n  }\n\n  .hero-background-decoration {\n    height: 600px;\n    top: -200px;\n  }\n\n  .hero-image {\n    width: auto;\n    height: 450px;\n    position: relative;\n    right: 35px;\n    bottom: 45px;\n  }\n\n  .hero-image-text-container {\n    float: left;\n  }\n\n  .hero-info-container {\n    display: block;\n    background-color: white;\n    box-shadow: 2px 4px 5px grey;\n    padding: 25px;\n  }\n\n  .hero-about-me {\n    font-size: clamp(2rem, 5vw + 5px, 6rem);\n    text-align: center;\n  }\n\n  .hero-paragraph {\n    font-size: 1.7rem;\n  }\n\n  .drake-header-text {\n    position: absolute;\n    top: -60px;\n    bottom: 0px;\n    left: 50px;\n    font-size: 3rem;\n    text-wrap: nowrap;\n    z-index: 1;\n  }\n\n  .hero-icons {\n    display: none;\n  }\n\n  main {\n    margin-top: 0rem;\n    padding: 20px;\n  }\n\n  .main-heading-text {\n    font-size: 2.5rem;\n  }\n\n  .projects-container {\n    grid-template-columns: 1fr 1fr;\n  }\n\n  .screenshot {\n    height: 220px;\n  }\n\n  .proj-info {\n    height: 150px;\n  }\n\n  .proj-description {\n    font-size: 1rem;\n    line-height: normal;\n  }\n\n  footer {\n    margin-top: 11rem;\n    padding: 2.5rem;\n    flex-direction: row;\n    justify-content: space-evenly;\n  }\n\n  .footer-image {\n    height: 300px;\n    width: 500px;\n  }\n\n  .footer-main-text {\n    font-size: 2rem;\n  }\n\n  .footer-secondary-text {\n    font-size: 1.7rem;\n    margin-top: 2rem;\n  }\n\n  .footer-links {\n    margin-top: 2rem;\n    gap: 1.5rem;\n  }\n}\n\n/* Desktop Query */\n\n@media screen and (min-width: 1000px) {\n  .hero-background-decoration {\n    transform: skew(0deg, -10deg);\n  }\n\n  .hero-container {\n    display: flex;\n    flex-direction: row;\n    margin-top: 0;\n    padding: 3rem;\n  }\n\n  .hero-info-container {\n    display: 